body {
  background-color: azure;
  font-family: sans-serif;
}

.form {
  text-align: center;
}

.input {
  border-radius: 15px;
  padding: 10px;
  font-size: 20px;
}

.btn {
  color: #fff;
  cursor: pointer;
  background-color: #59c0ed;
  border: none;
  border-radius: 15px;
  padding: 10px;
  font-size: 20px;
}

.js-news-container {
  flex-wrap: wrap;
  justify-content: center;
  align-items: start;
  gap: 10px;
  margin-top: 10px;
  display: flex;
}

.article-card {
  width: 30%;
  text-align: center;
  background-color: beige;
  border-radius: 15px;
  padding: 20px;
}

@media screen and (max-width: 770px) {
  .article-card {
    width: 100%;
  }
}

.article-img {
  width: 100%;
  border-radius: 15px;
}

.article-link {
  text-transform: uppercase;
  background-color: #faebd7;
  border-radius: 15px;
  padding: 10px;
  text-decoration: none;
  display: inline-block;
}

